import Swiper from 'swiper';
import { Navigation } from 'swiper/modules';
Swiper.use([Navigation]);
export default function datesSlidersInit() {
    const datesSliders = document.querySelectorAll('[data-js="datesSlider"]');
    if (datesSliders.length < 1)
        return;
    datesSliders.forEach((slider) => {
        const datesSliderPrev = slider.querySelector('[data-js="datesSliderPrev"]');
        const datesSliderNext = slider.querySelector('[data-js="datesSliderNext"]');
        const datesSliderEl = slider.querySelector('[data-js="datesSliderSlider"]');
        const swiperParams = {
            slidesPerView: 3,
            spaceBetween: 80,
            navigation: {
                nextEl: datesSliderNext,
                prevEl: datesSliderPrev,
            },
            watchOverflow: true,
        };
        const datesSliderEx = new Swiper(datesSliderEl, swiperParams);
    });
}
