import Swiper from 'swiper';
import { Thumbs, EffectFade } from 'swiper/modules';
Swiper.use([Thumbs, EffectFade]);
export default function tabsBlockInit() {
    const tabsBlocks = document.querySelectorAll('[data-js="tabsBlock"]');
    if (tabsBlocks.length < 1)
        return;
    tabsBlocks.forEach((tabsBlock) => {
        const slides = tabsBlock.querySelector('[data-js="tabsBlockSlides"]');
        const tabs = tabsBlock.querySelector('[data-js="tabsBlockList"]');
        const tabsPrev = tabsBlock.querySelector('[data-js="tabsBlockPrev"]');
        const tabsNext = tabsBlock.querySelector('[data-js="tabsBlockNext"]');
        const tabsParams = {
            slidesPerView: 'auto',
            watchSlidesProgress: true,
        };
        const tabsSliderEx = new Swiper(tabs, tabsParams);
        const slidesParams = {
            slidesPerView: 1,
            spaceBetween: 100,
            allowTouchMove: false,
            simulateTouch: false,
            effect: 'fade',
            thumbs: {
                swiper: tabsSliderEx,
            },
            navigation: {
                nextEl: tabsNext,
                prevEl: tabsPrev,
            },
        };
        const slidesSliderEx = new Swiper(slides, slidesParams);
    });
}
